@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@500&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;;
  padding: 0;
  font-family: 'Mulish', sans-serif;
}
.navbar{
  background-color: white;
}
.navbar-brand{
  font-size: 1.3rem;
  color: #565387;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565384;
}
.navbar a:hover{
  color: #3498db;
}





#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color:#3498db;
}
#header h2{
  color:#484848;
  font-size: 24px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: .5s;
  color:#3498db;
  border:2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: #3498db;
  color: white;
}

.card{
  box-shadow: 0px 0px 9px #3498db;
}
.card-img-top {
  width: 100%;
  height: 15vw;
  object-fit: cover;
}
.Simg{
  border-radius: 0;
  width:  60px;

}
img{

  border-radius: 15px;
}
.header-img{
 
  text-align: right;
}
#header .animated{
  animation: up-down 2s infinite ease-in-out alternate-reverse both ;
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}

@media(max-width:991px){
  .navbar-brand{
    font-size: 1.5rem;
  }
  .menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
